	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"What's that noise?","date":"25 Oct 2001","slug":"/2001-10-25-whats-that-noise/","tags":"Y2001"},"html":"<p>It used to be that during the week we could wake up in peace, shower, get dressed, fix breakfast, and then wake Christopher at 7am to get him ready for the day. These days Christopher is awake before either of us. </p>\n<p>For once last evening the ARD had a special report on a topic other than terrorism or Afghanistan. But the tunnel crash in Switzerland was not exactly a pleasant topic either.</p>"},"next":{"fields":{"title":"Welcome to the real world","date":"23 Oct 2001","slug":"/2001-10-23-welcome-to-the-real-world/"}},"previous":{"fields":{"title":"Space Park","date":"27 Oct 2001","slug":"/2001-10-27-space-park/"}}},{"node":{"fields":{"title":"Welcome to the real world","date":"23 Oct 2001","slug":"/2001-10-23-welcome-to-the-real-world/","tags":"Y2001"},"html":"<p>A hearty welcome to the world to <a href=\"http://hal.editthispage.com/stories/storyReader$979\">Ian Lovell Rager</a>! And congratulations to Hal and Audrey. The adventure begins.</p>\n<p>Christopher's reading list: <a href=\"http://www.amazon.com/exec/obidos/ASIN/0763615757\">Ten in the Bed!</a></p>\n<p><a href=\"http://www.amazon.com/exec/obidos/ASIN/0763615757\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/10/tenbed.jpg\" height=\"157\" width=\"200\" border=\"0\" alt=\"tenbed.jpg: \" /></a></p>"},"next":{"fields":{"title":"Can You Pronounce Wowereit?","date":"22 Oct 2001","slug":"/2001-10-22-can-you-pronounce-wowereit/"}},"previous":{"fields":{"title":"What's that noise?","date":"25 Oct 2001","slug":"/2001-10-25-whats-that-noise/"}}},{"node":{"fields":{"title":"Can You Pronounce Wowereit?","date":"22 Oct 2001","slug":"/2001-10-22-can-you-pronounce-wowereit/","tags":"Y2001"},"html":"<p>German vocabulary: pop-rivet tool is \"die Blindnietzange\".</p>\n<p>AP: <a href=\"http://dailynews.yahoo.com/h/ap/20011022/wl/berlin_election_5.html\">Berlin Election Boosts Ex-Communists</a></p>\n<p>Reuters: <a href=\"http://dailynews.yahoo.com/h/nm/20011021/wl/germany_election_berlin_dc_2.html\">Berlin Win Bodes Well for Schroeder Reelection Bid</a></p>\n<p>Watching the CDU lose nearly half their vote share in Berlin was particularly gratifying. So who's going to tell Dubya that ex-communists could be coalition partners in the capital of a major ally?</p>"},"next":{"fields":{"title":"The Berlin Wahl","date":"21 Oct 2001","slug":"/2001-10-21-the-berlin-wahl/"}},"previous":{"fields":{"title":"Welcome to the real world","date":"23 Oct 2001","slug":"/2001-10-23-welcome-to-the-real-world/"}}}],"pathPrefix":"","first":false,"last":false,"index":966,"pageCount":1101}}

